<template>
	<div class="detailer" >
		<div>
			<contents :movie="movie" ></contents>
		</div>
		<div class="text">
			<h1>{{movie.filmName}}</h1>
			<h3>评分:{{movie.score}}</h3>
			<h5>类型:{{movie.typeV}}</h5>
			<p>导演:{{movie.director}}</p>
			<p>主演:{{movie.actV}}</p>
			<p>时长:{{movie.duration}}分钟</p>
			<p>语言:{{movie.languageV}}</p>
			<p>语言:{{movie.languageV}}</p>
			<p>上映时间:{{movie.releaseDate}}</p>
			<p>简介:{{movie.synopsis}}</p>
			<a :href="mvlink">片源:{{mvname}}</a>
		</div>
	</div> 
</template>

<script>
// import bus from '../detail.js'
import axios from 'axios'
import contents from '@/components/vue-movie/common/contents.vue'

	export default{
		name:'Detailer',
		components:{
			contents
		},
		data() {
			return{
				movie:[],
				id:'',
				movieLink:[],
				mvname:'',
				mvlink:''
			}
		},
		
		mounted() {
			this.id = this.$route.query.movieId
			this.mvDetail()
		},
		methods:{
			mvDetail(){
				axios({
					method:"get",
					url:"http://localhost:8888/filmsdata/byid",
					params:{
						id:this.id
					}
				}).then(r=>{
					this.movie = r.data
					this.movieLink = r.data.linksV
					console.log(this.movieLink)
					this.mvname =  Object.keys(r.data.linksV)
					this.mvlink =  Object.values(r.data.linksV)
				})
			},
			
		}
	}
</script>

<style scoped="scoped">
	.detailer{
		width: 100%;
		background-color: #272727;
		margin-top: 1vh;
		display: flex;
		flex-direction: row;
	}
	.text{
		padding: 10vw;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		color: white;
		width: 50vw;
	}
	a{
		text-align: left;
		word-break: break-all;
		word-wrap: break-word
	}
	p{
		line-height: 2em;
		text-align: left;
	}
</style>
